const STARTING = [];

function moveUnderscoresToFront(arr) {
  let result = [];
  for (let c of arr) {
    if (!'AEIOU_'.includes(c)) { result.push(c); }
  }
  let consonantCount = result.length;
  for (let i = 0; i < arr.length - consonantCount; i++) { result.unshift('_'); }
  return result;
}

const Home = () => {
  let [ lettersData, setLettersData ] = useState({
    letters: STARTING,
    order: STARTING.map((item, i) => ({ letter: item, origIdx: i })),
    answer: '',
    answers: [],
    allWords: [],
    showAnswer: false,
    showGiveupButton: false,
    loading: true,
  });

  let setOrder = (newOrder) => setLettersData({
    ...lettersData,
    order: newOrder
  });

  let getSameFormWords = (originalWord, allWords) => {
    let wordLength = originalWord.length;
    let wordConsonants = originalWord.split('')
                         .map(c => c.toUpperCase())
                         .filter(c => !'AEIOU'.includes(c));
    
    return allWords.filter(word => {
      let length = word.length;
      let consonants = word.split('')
                         .map(c => c.toUpperCase())
                         .filter(c => !'AEIOU'.includes(c));
      return length === wordLength && _.isEqual(consonants, wordConsonants);
    });
  };

  let setNewWord = (word) => {
    let items = word.split('')
                    .map(c => c.toUpperCase())
                    .map(c => 'AEIOU'.includes(c) ? '_' : c );
         
    items = moveUnderscoresToFront(items);
    setLettersData({
      ...lettersData,
      letters: items,
      order: items.map((item, i) => ({ letter: item, origIdx: i })),
      answer: word,
      answers: getSameFormWords(word, lettersData.allWords),
      showAnswer: false,
    });

    setTimeout(() => {
      setLettersData(lettersData => ({
        ...lettersData,
        showGiveupButton: true,
      }));
    }, 1500);
  };

  const getAnswerOrder = (order, answer) => {
    let result = [];
    let clonedOrder = order.slice();

    // if (order.map(o => o.letter).join('') === answer) { return order; }
    
    for (let c of answer.split('')) {
      let foundEntry = clonedOrder.find(o => o.letter === c)
        || (clonedOrder.find(o => o.letter === '_') || clonedOrder.find(o => 'AEIOU'.includes(o.letter)));
      foundEntry.letter = c;
      result.push(foundEntry);
      clonedOrder = clonedOrder.filter(o => o.origIdx !== foundEntry.origIdx);
    }

    return result;
  };

  useEffect(() => {
    async function fetchData() {
      const data = popularWords;
      let words = data.filter(w => w.length >= 5 && w.length <= 7).map(w => w.toUpperCase());

      let word = sample(words);

      let items = word.split('')
        .map(c => c.toUpperCase())
        .map(c => 'AEIOU'.includes(c) ? '_' : c );

      items = moveUnderscoresToFront(items);

      setTimeout(() => {
        setLettersData({
          ...lettersData,
          allWords: words,
          letters: items,
          order: items.map((item, i) => ({ letter: item, origIdx: i })),
          answer: word,
          answers: getSameFormWords(word, words),
          loading: false,
          showGiveupButton: false,
        });
      }, 500);

      setTimeout(() => {
        setLettersData(lettersData => ({
          ...lettersData,
          showGiveupButton: true,
        }));
      }, 1500);
    }
    fetchData();
  }, []);

  let onCorrect = () => {
    setNewWord(sample(lettersData.allWords));
    setBackgroundColor('STANDBY');
  };

  const coloredBackgroundStyle = {
    height: '100vh',
    width: '100vw',
    position: 'absolute',
  };
  const pages = [
    ({ style }) => <animated.div style={{ ...style, background: 'lightcoral', ...coloredBackgroundStyle }}></animated.div>,
    ({ style }) => <animated.div style={{ ...style, background: 'mediumturquoise', ...coloredBackgroundStyle }}></animated.div>,
    ({ style }) => <animated.div style={{ ...style, background: 'springgreen', ...coloredBackgroundStyle }}></animated.div>,
  ];

  const [index, set] = useState(1)

  const transitions = useTransition(index, p => p, {
    from: { opacity: 0, },
    enter: { opacity: 1, },
    leave: { opacity: 0, },
  });

  const setBackgroundColor = (state) => {
    if (state === 'STANDBY') { return set(1); }
    if (state === 'ANSWERED') { return set(2); }
    if (state === 'CHEAT') { return set(0); }
  };

  const hideGiveupButton = () => {
    setLettersData(oldLettersData => ({
      ...oldLettersData,
      showGiveupButton: false,
    }));
  }
  
  return <div className="app">
    <Head>
      <title>Vowels</title>
      <link rel="icon" href="/favicon.ico" />
    </Head>
    <h1 style={{ zIndex: 50, position: "absolute", top: 10, left: 10, fontSize: '40px'}}> Move the Yellow Blocks to Form Words</h1>
    <div className="colored-background" style={{ position: 'absolute', top:'0', left:'0' }}>
      {transitions.map(({ item, props, key }) => {
        const Page = pages[item]
        return <Page key={key} style={props} />
      })}
    </div>
    <DraggableList {...{ order: lettersData.order, setOrder }} showAnswer={lettersData.showAnswer}
      items={lettersData.letters} answer={lettersData.answer} answers={lettersData.answers} setBackgroundColor={setBackgroundColor}
      onCorrect={onCorrect} loading={lettersData.loading} hideGiveupButton={hideGiveupButton}/>

    <div className="bottom-buttons">
      <animated.button style={useSpring({ opacity: lettersData.showGiveupButton ? 1 : 0 })} className="answer-button" onClick={_.throttle(() => {
        if (!lettersData.showGiveupButton) { return; }
        if (lettersData.showAnswer) { return; }
        if (lettersData.order.map(o => o.letter).join('') === lettersData.answer) { return; }
        setLettersData({
          ...lettersData,
          showAnswer: true,
          order: getAnswerOrder(lettersData.order, lettersData.answer),
          showGiveupButton: false,
        });
      }, 1000)}>GIVE UP</animated.button>
    </div>
    { globalStyle }
  </div>
};

export default Home
